import Dialog from '../../wxcomponents/vant/dist/dialog/dialog';
var app = getApp().globalData
export default {
	data() {
		return {
			showTitle3:false,
			showTitle2:false,
			showTitle1:false,
			myClub:[],
			tabIndex:0,
			tabList:['全球总榜','城市榜','俱乐部榜'],
			global_page: 1, // 全球榜分页
			global_list: [], // 全球榜
			global_mylist: [],
			loading1:false,
			allow1:false,
			
			china_page: 1, // 城市分页
			china_list: [], // 城市榜
			china_mylist: [],
			loading2:false,
			allow2:false,
			
			club_page: 1, // 俱乐部分页
			club_list: [], // 俱乐部榜
			club_mylist: [],
			loading3:false,
			allow3:false
		}
	},
	onShareAppMessage:function(res) {
		return {
		  title: '交易额',
		  imageUrl:app.shareImg,
		  path: '/pagesTwo/TradingVolume/TradingVolume'
		}
	},
	onLoad() {
		this.my()
		this.getGlobal()
		this.getChina()
	},
	// 下拉刷新
	onPullDownRefresh() {
		if(this.tabIndex === 0){
			this.global_page = 1
			this.getGlobal()
			setTimeout(function () {
				uni.stopPullDownRefresh();
				uni.showToast({
					title: '刷新成功',
					duration: 1000,
					icon:'none'
				});
			}, 1000);
		}
		
		if(this.tabIndex === 1){
			this.china_page = 1
			this.getChina()
			setTimeout(function () {
				uni.stopPullDownRefresh();
				uni.showToast({
					title: '刷新成功',
					duration: 1000,
					icon:'none'
				});
			}, 1000);
		}
		
		if(this.tabIndex === 2){
			this.club_page = 1
			this.getClub()()
			setTimeout(function () {
				uni.stopPullDownRefresh();
				uni.showToast({
					title: '刷新成功',
					duration: 1000,
					icon:'none'
				});
			}, 1000);
		}

	},
	// 触底加载
	onReachBottom(){
		// 第一页的上拉加载
		if(this.tabIndex ===0 && this.allow1){
			this.allow1 = false
			this.loading1 = true
			this.global_page++
			this.getGlobal()
			// setTimeout(() => {
			// 	this.global_page++
			// 	this.getGlobal()
			// 	uni.showToast({
			// 		title: '加载成功',
			// 		duration: 1000,
			// 		icon:'none'
			// 	});
			//   }, 1500)	
		}
		
		if(this.tabIndex ===1 && this.allow2){
			this.allow2 = false
			this.loading2 = true
			this.china_page++
			this.getChina()
			// setTimeout(() => {
			// 	this.china_page++
			// 	this.getChina()
			// 	uni.showToast({
			// 		title: '加载成功',
			// 		duration: 1000,
			// 		icon:'none'
			// 	});
			//   }, 1500)	
		}
		
		if(this.tabIndex ===2 && this.allow3){
			this.allow3 = false
			this.loading3 = true
			this.club_page++
			this.getClub()()
			// setTimeout(() => {
			// 	this.club_page++
			// 	this.getClub()()
			// 	uni.showToast({
			// 		title: '加载成功',
			// 		duration: 1000,
			// 		icon:'none'
			// 	});
			//   }, 1500)	
		}

	},
	methods: {
		goClub(id){
			uni.navigateTo({
				url:'../../pagesTwo/TradingClub/TradingClub?id='+id
			})
		},
		goCity(name){
			uni.navigateTo({
				url:'../../pagesTwo/TradingCity/TradingCity?name='+name
			})
		},
		goHistory(id){
			uni.navigateTo({
				url:'../../pagesTwo/TradingHistory/TradingHistory?id='+id
			})
		},
		MyInfo(id){
			uni.navigateTo({
				url:'../../pagesMy/MyInfo/MyInfo?id='+id
			})
		},
		// 发布跳转
		Issue(){
			if(uni.getStorageSync('setInfo') ===0){
				uni.showToast({
				    title: '请完善个人信息',
				    duration: 2000,
					icon:'none'
				});
				
				setTimeout(function() {
					uni.navigateTo({
						url:'../../pages/PersonalIdcard/PersonalIdcard'
					})
				}, 300);
				
			}else{
				if (uni.getStorageSync('isVip') === 0) {
					Dialog.confirm({
					  title: '提示',
					  message: '您还不是VIP用户,没有发布权限',
					  confirmButtonText:'去支付'
					}).then(() => {
						uni.navigateTo({
							url:'../../pagesMy/pay/pay'
						})
					}).catch(() => {
					  // on cancel
					});
				  } else if (uni.getStorageSync('isVip') === 1) {
					uni.navigateTo({
						url:'../../pagesTwo/TradingVolumeSend/TradingVolumeSend'
					})
				  } else {
					uni.clearStorageSync()
					uni.showToast({
					    title: '请登录',
					    duration: 2000,
						icon:'none'
					});
					
					uni.reLaunch({
						url: '../../pages/login/login'
					})
				  }
			}
			
		},
		// 全球榜
		getGlobal(){
			this.$api.Turnover_index({
				token: uni.getStorageSync("token"),
				user_id: uni.getStorageSync("userId"),
				type: 0,
				page: this.global_page
		    }).then(res => {
				// console.log(res)
				if(res.data.code === '200'){
					if(this.global_page === 1){
						if(res.data.data.data.length>=30){
							this.global_list = res.data.data.data
							this.global_mylist = res.data.data.now
							this.allow1 = true
							this.loading1 = false
						}else{
							this.global_list = res.data.data.data
							this.global_mylist = res.data.data.now
							this.allow1 = false
							this.loading1 = false
							this.showTitle1 = true
						}														
					}else{
						if(res.data.data.data.length>=30){
							this.global_list = this.global_list.concat(res.data.data.data)
							this.allow1 = true
							this.loading1 = false
						}else{
							this.global_list = this.global_list.concat(res.data.data.data)
							this.allow1 = false
							this.loading1 = false
							this.showTitle1 = true
						}	
					}
				}
			})
		},
		// 城市榜
		getChina(){
			this.$api.Turnover_index({
				token: uni.getStorageSync("token"),
				user_id: uni.getStorageSync("userId"),
				type: 1,
				page: this.china_page,
				city_name:uni.getStorageSync('city')
		    }).then(res => {
				// console.log(res)
				if(res.data.code === '200'){
					if(this.china_page === 1){
						if(res.data.data.data.length>=30){
							this.china_list = res.data.data.data
							this.china_mylist = res.data.data.now
							this.allow2 = true
							this.loading2 = false
						}else{
							this.china_list = res.data.data.data
							this.china_mylist = res.data.data.now
							this.allow2 = false
							this.loading2 = false
							this.showTitle2 = true
						}														
					}else{
						if(res.data.data.data.length>=30){
							this.china_list = this.china_list.concat(res.data.data.data)
							this.allow2 = true
							this.loading2 = false
						}else{
							this.china_list = this.china_list.concat(res.data.data.data)
							this.allow2 = false
							this.loading2 = false
							this.showTitle2 = true
						}	
					}
				}
			})
		},
		// 俱乐部榜
		getClub(){
			this.$api.Turnover_index({
				token: uni.getStorageSync("token"),
				user_id: uni.getStorageSync("userId"),
				type: 2,
				page: this.club_page,
				club_data:JSON.stringify(this.myClub)
		    }).then(res => {
				// console.log(res)
				if(res.data.code === '200'){
					if(this.club_page === 1){
						if(res.data.data.data.length>=30){
							this.club_list = res.data.data.data
							this.club_mylist = res.data.data.now
							this.allow3 = true
							this.loading3 = false
						}else{
							this.club_list = res.data.data.data
							this.club_mylist = res.data.data.now
							this.allow3 = false
							this.loading3 = false
							this.showTitle3 = true
						}														
					}else{
						if(res.data.data.data.length>=30){
							this.club_list = this.club_list.concat(res.data.data.data)
							this.allow3 = true
							this.loading3 = false
						}else{
							this.club_list = this.club_list.concat(res.data.data.data)
							this.allow3 = false
							this.loading3 = false
							this.showTitle3 = true
						}	
					}
				}
			})
		},
		nav(ind){
			this.tabIndex = ind
		},
		my(){
			this.$api.Myself_index({
				token: uni.getStorageSync("token"),
				user_id: uni.getStorageSync("userId")
			}).then(res=>{
				this.myClub = res.data.data.club_data
				this.getClub()
			})
		}
	}
}
